







import { Component, Emit, Prop, Vue } from "vue-property-decorator";

@Component
export default class MyButton extends Vue {
  public initialValue!: string;

  @Prop() private msg!: string;
  @Prop() public greet?: string;

  @Emit() public clicked() {}

  public onClick(): void {
    this.greet = "おはよう！";
    alert(this.greet);
    this.clicked();
  }
}
