



























import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import MyButton from "@/components/MyButton.vue";
import RaiseButton from "@/components/RaiseButton.vue";

@Component({
  components: {
    MyButton,
    RaiseButton,
    HelloWorld
  }
})
export default class HelloWorld extends Vue {
  @Prop() private msg!: string;
  @Prop() private counter: number = this.$store.getters.getCount;
  public greetText: string = "おはよう！";
  public pushContent: string = "リセット";

  public onMyButtonClicked() {
    this.greetText = "おはよう！";
  }
  public onRaiseButtonClicked() {
    this.greetText = "こんばんは！";
    alert(this.greetText);
  }
  public onNowButtonClicked() {
    alert(this.greetText);
  }
  public onClearClicked() {
    this.$store.dispatch("clear");
    this.counter = this.$store.getters.getCount;
    new Notification("初期化しました。");
  }
  public onCountClicked(num: number) {
    if (num < 0) {
      this.$store.dispatch("decrement");
    } else {
      this.$store.dispatch("increment");
    }
    this.counter = this.$store.getters.getCount;
  }

  mounted() {
    // 読込み時にPush通知の拒否or許可がされていなければ要求メッセージを出す
    if ("Notification" in window) {
      const permission = Notification.permission;
      if (permission !== "denied" && permission !== "granted") {
        Notification.requestPermission();
      }

      // カウンターを定周期で回す
      setInterval(this.loopIncrement, 5000);
    }
  }

  private async loopIncrement() {
    await this.$store.dispatch("increment");
    this.counter = this.$store.getters.getCount;
  }

  @Watch("counter")
  public counterChanged(): void {
    if (this.counter % 5 === 0) {
      new Notification(this.counter + "のPushだよ");
    }
  }
}
