import Vue from "vue";
import Vuex, { mapGetters } from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    count: 0,
    intervalId: null
  },
  mutations: {
    increment: state => state.count++,
    decrement: state => state.count--,
    clear: state => (state.count = 0)
  },
  actions: {
    increment(state) {
      state.commit("increment");
    },
    decrement(state) {
      state.commit("decrement");
    },
    clear(state) {
      state.commit("clear");
    }
  },
  getters: {
    getCount(state): number {
      return state.count;
    }
  }
});
